/**brnn gameView */
import { events, EventKind } from '../brnntools/brnn_event';
import { configuration } from '../brnnutils/brnnConfiguration';
import { brnnanimate } from './brnnAnimate';
import { audiotool } from '../brnnutils/brnnAudioTool';
import { brnnglobal } from '../brnnutils/brnnGlobal';
import { brnnutils } from '../brnnutils/brnnUtils';
const { ccclass, property } = cc._decorator;

let rankTemp = cc.Class({
    name: 'rankTemp',
    properties: {
        id: 0,
        nickName: '',
        thirdAvatarID: 0,
        location: "",
        division: "",
        challengeCoin: 10,
    }
});

@ccclass
export default class brnnGameView extends cc.Component {
    @property({ type: cc.Prefab, tooltip: 'Prefab' })
    onlinePanl: cc.Prefab = null;

    @property({ type: cc.Prefab, tooltip: 'item' })
    err_item: cc.Prefab = null;

    @property({ type: cc.Node, tooltip: '' })
    btn_sound: cc.Node = null;

    @property({ type: cc.Node, tooltip: '' })
    btn_mute: cc.Node = null;

    @property({ type: cc.Node, tooltip: '' })
    bot_layer: cc.Node = null;

    @property({ type: cc.Button, tooltip: '' })
    grp_chip_btn: cc.Button[] = [];

    @property({ type: cc.Node, tooltip: '' })
    bet_region: cc.Node[] = [];

    @property({ type: cc.Node, tooltip: '' })
    card_node: cc.Node[] = [];

    @property({ type: cc.Node, tooltip: '' })
    addonlin_node: cc.Node = null;

    @property({ type: cc.Node, tooltip: '' })
    ratePanl: cc.Node = null;

    @property({ type: cc.Node, tooltip: '' })
    rulePanl: cc.Node = null;

    FloatWordlayer: cc.Node = null;
    tipbg: cc.Sprite = null;

    public online_panl: cc.Node = null;

    public head_player_8 = null;
    public Promptbox = null;
    public updateBetRegionPos: any[] = [null, null, null, null];

    public initBetRegionPos: any[] = [];
    public designResolutionW: number = 1334;//
    public designResolutionH: number = 750;
    @property({ type: rankTemp, tooltip: '' })
    rankTemps = [];

    private aArray: number[] = [1, 2, 3, 4];

    private BGState:boolean = true;

    // static instance: UIMaskManager = null;
    // static GetInstance() {
    //     if (this.instance == null) {
    //         this.instance = new cc.Node("UIMaskManager").addComponent(this);
    //     }
    //     return this.instance;
    // }

    init() {
        let self = this;
        this.Promptbox = cc.find('Canvas/Promptbox');
        this.head_player_8 = cc.find('Canvas/Centerlayer/head_player_8');
        this.tipbg = cc.find('Canvas/Tips/p_trumpet_bg').getComponent(cc.Sprite);
        this.resize();
        for (let value of this.aArray) {
            cc.log('hahahahahaaaaaaaa', value);
        }
    }

    onLoad() {
        this.init();
        this.head_player_8.on('touchend', () => { this.onclick('head_player_8'); })
        events.register(EventKind.S2C_OnlineP, "brnnGameView", this.addOnlineList.bind(this));//
        events.register(EventKind.S2C_History, "brnnGameView", this.roomInfo.bind(this));
        events.register(EventKind.S2C_Err, "brnnGameView", this.getErrMsg.bind(this));

        // events.register(EventKind.S2C_ServerconnectClose, "brnnGameView", this.serverClose.bind(this));

        // let action1 = cc.cardinalSplineTo(5, [0, 0], 0);

    }
    start() {
        this.whetherPlayAudio();
    }
    /**
    * 
    */
    whetherPlayAudio() {
        if( !cc.isValid( this.btn_sound ) ||
            !cc.isValid( this.btn_mute ) )
        {
            return;
        }
        
        if (this.BGState == true) {
            this.btn_sound.active = true;
            this.btn_mute.active = false;
            audiotool.volume_iudioSource(false);
            this.node.getComponent(cc.AudioSource).mute = false;
        } else {
            this.btn_sound.active = false;
            this.btn_mute.active = true;
            audiotool.volume_iudioSource(true);
            this.node.getComponent(cc.AudioSource).mute = true;
        }
    }
    /** */
    private resize() {
        let self = this;
        /** */
        for (let i = 0; i < this.grp_chip_btn.length; i++) {
            this.grp_chip_btn[i].node.x = cc.winSize.width * (this.grp_chip_btn[i].node.x / this.designResolutionW);
        }
        this.tipbg.node.width = cc.winSize.width * (this.tipbg.node.width / this.designResolutionW);
        this.tipbg.node.height = cc.winSize.height * (this.tipbg.node.height / this.designResolutionH);

        /** */
        for (let j = 0; j < this.bet_region.length; j++) {
            this.bet_region[j].x = cc.winSize.width * (this.bet_region[j].x / this.designResolutionW);
            this.bet_region[j].y = cc.winSize.height * (this.bet_region[j].y / this.designResolutionH);
            this.initBetRegionPos.push(this.bet_region[j].position);
            this.bet_region[j].width = cc.winSize.width * (this.bet_region[j].width / this.designResolutionW);
            this.bet_region[j].height = cc.winSize.height * (this.bet_region[j].height / this.designResolutionH);
            for (let k = 0; k < this.bet_region[j].childrenCount; k++) {
                this.bet_region[j].children[k].x = cc.winSize.width * (this.bet_region[j].children[k].x / this.designResolutionW);
                this.bet_region[j].children[k].y = cc.winSize.height * (this.bet_region[j].children[k].y / this.designResolutionH);

            }
        }
        /** */
        for (let m = 1; m < this.card_node.length; m++) {
            this.card_node[m].x = cc.winSize.width * (this.card_node[m].x / this.designResolutionW);
            this.card_node[m].y = cc.winSize.height * (this.card_node[m].y / this.designResolutionH);
        }

    }
    private onclick(targetname: string) {
        audiotool.play_audioSource(audiotool.LoadAudio(24));
        if (targetname == "head_player_8") {
            let msg = {
                id: 15
            }
            events.dispatch(EventKind.C2S_OnlineP, msg);
        }
    }

    touchSoundMuteBtn(event, customIndex) {
        if( !cc.isValid( this.btn_sound ) ||
            !cc.isValid( this.btn_mute ) )
        {
            return;
        }
        if (customIndex == 0) {
            audiotool.play_audioSource(audiotool.LoadAudio(24));
            this.scheduleOnce( ()=>{
                                        if( !cc.isValid( this.btn_sound ) ||
                                            !cc.isValid( this.btn_mute ) )
                                        {
                                            return;
                                        }
                                        brnnglobal.isSoundOpen = false;
                                        this.btn_sound.active = false;
                                        this.btn_mute.active = true;
                                        audiotool.volume_iudioSource(true);
                                        this.node.getComponent(cc.AudioSource).mute = true;
                                        this.BGState = false;
                                    } , 0.3 )
        } else {
            brnnglobal.isSoundOpen = true;
            this.btn_sound.active = true;
            this.btn_mute.active = false;
            audiotool.volume_iudioSource(false);
            this.node.getComponent(cc.AudioSource).mute = false;
            this.BGState = true;
        }
    }

    /** */
    private addOnlineList(xdata: any) {
        let self = this;
        if(!cc.isValid( this.addonlin_node ) )return;
        this.addonlin_node.active = true;
        if (xdata == null) return;
        let mdata = JSON.parse(xdata.data);
        if (this.online_panl != null) {
            this.online_panl.removeFromParent(false);
        }
        this.online_panl = cc.instantiate(this.onlinePanl);
        let brnnonlinePanl = this.online_panl.getComponent('brnnonlinePanl');
        brnnonlinePanl.onLinePlayerList(mdata);
        this.addonlin_node.addChild(this.online_panl);
        let baak_btn = this.online_panl.getChildByName('p_btn_back');
        baak_btn.on('touchend', () => {
            if(!cc.isValid( this.addonlin_node ) )return;
            audiotool.play_audioSource(audiotool.LoadAudio(24));
            this.addonlin_node.removeChild(this.online_panl);
            this.addonlin_node.active = false;
        })
    }

    /** */
    public roomInfo(xdata: any) {
        if (!xdata) {
            return;
        }
        cc.log('');
        configuration.outInitListHistory(xdata);
    }
    /**
     * @param err=0 
     * @param err=1
     * @param err=2
     * @param err=3
     * @param err=4
     * @param err=5
     * @param err=6
     * @param err=7
     * @param err=8
     * @param err=9
     * @param err=10
     * @param err=11
     * @param err=12
     * @param err=13
     * @param err=14
     * @param err=15
     */
    private getErrMsg(xdata: any) {
        if (!xdata) return;
        let mdata = xdata.resultCode;
        if (!mdata) return;
        let err_tip = cc.instantiate(this.err_item);
        let tip_lab = err_tip.getChildByName('tip_lab');
        if (mdata == 1) {
            tip_lab.getComponent(cc.Label).string = '';
            this.node.addChild(err_tip);
            err_tip.getComponent('promptbox').initPrompt();
        } else if (mdata == 2) {
            tip_lab.getComponent(cc.Label).string = '';
            this.node.addChild(err_tip);
            err_tip.getComponent('promptbox').initPrompt();
        } else if (mdata == 3) {
            tip_lab.getComponent(cc.Label).string = '';
            this.node.addChild(err_tip);
            err_tip.getComponent('promptbox').initPrompt();
        } else if (mdata == 4) {
            tip_lab.getComponent(cc.Label).string = '';
            this.node.addChild(err_tip);
            err_tip.getComponent('promptbox').initPrompt();
        } else if (mdata == 5) {
            tip_lab.getComponent(cc.Label).string = '';
            this.node.addChild(err_tip);
            err_tip.getComponent('promptbox').initPrompt();
        } else if (mdata == 6) {
            tip_lab.getComponent(cc.Label).string = '';
            this.node.addChild(err_tip);
            err_tip.getComponent('promptbox').initPrompt();
        }
    }

    private serverClose(data: any) {
        // let err_tip = cc.instantiate(this.err_item);
        // let tip_lab = err_tip.getChildByName('tip_lab');
        // tip_lab.getComponent(cc.Label).string = '';
        // this.node.addChild(err_tip);
        // err_tip.getComponent('promptbox').initPrompt();
    }
    update() {
    }
    onDestroy() {
        cc.audioEngine.stopAll();

        this.head_player_8.off('touchend', () => { this.onclick('head_player_8'); })

        events.unregister(EventKind.S2C_OnlineP, "brnnGameView");//
        events.unregister(EventKind.S2C_History, "brnnGameView");
        events.unregister(EventKind.S2C_Err, "brnnGameView");
        // events.unregister(EventKind.S2C_ServerconnectClose, "brnnGameView");

        this.initBetRegionPos = [];
    }

    /***
     * 
     * 
     * 
     * 
     * 
     * 
     * 
     * 
     * 
     * 
     * 
     * 
     */
}
