/**
 * 
 *   import Configuration from "./Configuration"
 */
class brnnUtils {

    protected static instance: brnnUtils;
    private MessageBoxPrefab:cc.Prefab = null;

    public static getInstance(): brnnUtils {
        if (!this.instance) {
            this.instance = new brnnUtils();
        }
        return this.instance;
    }
    addClickEvent(node, target, component, handler) {
        var eventHandler = new cc.Component.EventHandler();
        eventHandler.target = target;
        eventHandler.component = component;
        eventHandler.handler = handler;

        var clickEvents = node.getComponent(cc.Button).clickEvents;
        clickEvents.push(eventHandler);
    }

    setMessageBoxPrefab( prefab:cc.Prefab )
    {
        this.MessageBoxPrefab = prefab;
    }

    showMessageBox( parent:cc.Node , tips:string , fnCallbackOK:Function = null, fnCallbackCancel:Function = null , isOK:boolean = true , isCancel:boolean = false , isClose:boolean = false )
    {
        if( this.MessageBoxPrefab )
        {
            let messagebox = cc.instantiate( this.MessageBoxPrefab );
            if( cc.isValid( messagebox ) )
            {
                parent.addChild(messagebox,100);
                setTimeout( ()=>{
                    if( cc.isValid( messagebox ) )
                    {
                        let msgboxscript = messagebox.getComponent("brnnMessageBox");
                        if( cc.isValid( msgboxscript ) )
                        {
                            msgboxscript.init( tips , fnCallbackOK , fnCallbackCancel , isOK , isCancel , isClose );
                        }
                        messagebox.active = true;
                    }
                    else if( cc.isValid( fnCallbackCancel ) )
                    {
                        fnCallbackCancel();
                    }
                    } , 0.1 * 1000 )
            }
        }
    }

}
export const brnnutils = brnnUtils.getInstance();
