    /**
  * 
  * @param head 
  * @param pos 
  * @param index 
  */
    public HeadAnim(head: cc.Node, headpos: any, index: number) {
        if( cc.isValid( head ))
        {
            head.stopAllActions();
            if (index > 1 && index < 5) {
                var s1 = cc.moveTo(0.05, cc.v2(headpos.x + 39, headpos.y));
            } else {
                var s1 = cc.moveTo(0.05, cc.v2(headpos.x - 39, headpos.y));
            }
            var s2 = cc.moveTo(0.1, headpos);
            var seq = cc.sequence([s1, s2]);
            let call = cc.callFunc((sender) => {
                if( cc.isValid( sender ) )
                {
                    sender.stopAllActions();
                    sender.position = headpos;
                    this.lastPlayerHead = sender;
                }

            })
            let spew = cc.sequence(seq, call);
            head.runAction(spew);
        }
    }


    /**
        * 
        * 
        * @param betNum 
        * @param headPos 
        * @param area 
        * @param quantity   
        * @param itemarr[] 
        * @param areanum 
        */
    points_chip_move(betNum: number, headPos, area, quantity, itemarr: any, areanum: number) {
        if (betNum <= 0) {
            return;
        }
        let num = betNum;
        let count = 0;
        if (num >= 1000) {
            count = Math.floor(num / 1000);
            num = num % 1000;
            for (let i = 0; i < count; i++) {
                this.showCoins(headPos, area, itemarr[4], quantity, areanum);
            }
        }
        if (num >= 100) {
            count = Math.floor(num / 100);
            num = num % 100;
            for (let i = 0; i < count; i++) {
                this.showCoins(headPos, area, itemarr[3], quantity, areanum);
            }
        }
        if (num >= 50) {
            count = Math.floor(num / 50);
            num = num % 50;
            for (let i = 0; i < count; i++) {
                this.showCoins(headPos, area, itemarr[2], quantity, areanum);
            }
        }
        if (num >= 10) {
            count = Math.floor(num / 10);
            num = num % 10;
            for (let i = 0; i < count; i++) {
                this.showCoins(headPos, area, itemarr[1], quantity, areanum);
            }
        }
        if (num >= 1) {
            count = Math.floor(num / 1);
            num = num % 1;
            for (let i = 0; i < count; i++) {
                this.showCoins(headPos, area, itemarr[0], quantity, areanum);
            }
        }
    }
    /**
   * 
   * @param headPos 
   * @param area 
   * @param item 
   * @param quantity 
   * @param areanum 
   */
    public showCoins(headPos: any, area: cc.Node, item: any, quantity, areanum: number): void {
        if(!cc.isValid(item))return;
        let i = 0;
        let chilp = cc.instantiate(item);
        area.addChild(chilp);
        this.allchip_arr.push(chilp);
        this.correspond_chips_arr[areanum].push(chilp);
        var mpos: cc.Vec2 = cc.v2(0, 0);
        // mpos = headPos.position;     //

        // mpos = headPos.parent.convertToWorldSpaceAR(mpos);
        // // cc.log(" _ ", pos);
        // mpos = area.convertToNodeSpaceAR(mpos);
        mpos = headPos;     //
        // mpos = headPos.parent.convertToWorldSpaceAR(mpos)

        chilp.position = mpos;

        if( !cc.isValid( quantity ) )
        {
            console.log("showCoins  quantity is null");
            return;
        }
        let tos: cc.Vec2 = quantity.position;   //
        tos = quantity.parent.convertToWorldSpaceAR(tos);
        tos = area.convertToNodeSpaceAR(tos);

        let xx = configuration.random(-70, 70);
        let yy = configuration.random(-40, 40);

        tos.x += xx;
        tos.y += yy;
        this.chippos.push(tos);
        chilp.stopAllActions();
        chilp.runAction(
            cc.sequence(
                cc.moveTo(0.7, tos),
                cc.jumpBy(0.2, 4, 0, 2, 2),
                // cc.scaleTo(0.1, 1.2, 1.2),
                // cc.scaleTo(0.1, 1, 1),
                cc.callFunc( ()=>{
                    if( cc.isValid( chilp ) )
                    {
                        chilp.name = chilp.name + i;
                        i++;
                        chilp.stopAllActions();
                    }
                }, this)
            )
        );
    }
    /**
    * //
    * @param  headpos 
    * @param  win   
    */
    public chipback(headpos, win: number[]) {
        if (this.allchip_arr.length == 0) {
            cc.log('');
            return;
        } else {
            let num = Math.ceil(this.allchip_arr.length / win.length);
            let arr = configuration.chunk(this.allchip_arr, num);
            audiotool.play_audioSource(audiotool.LoadAudio(27));
            for (let i = 0; i < win.length; i++) {
                if (arr[i] == null || arr == null) return;
                else {
                    for (let a = 0; a < arr[i].length; a++) {
                        let postion: cc.Vec2 = headpos[win[i]];   //
                        let ac = cc.moveTo(0.5, postion);
                        let call = cc.callFunc((sender) => {
                            if( cc.isValid( sender ) )
                            {
                                sender.destroy();
                                sender = null;
                            }
                        })
                        let seq = cc.sequence(ac, call);
                        arr[i][a].stopAllActions();
                        arr[i][a].runAction(seq);
                    }
                }
            }
        }
    }
