    public bankerToTable(head: any[], win: number[], data: msg.GameAward, quan_Arr: cc.Node[], area: any, itemarr: any[], fun: Function) {    //
        if( this.chipCount > 0 )
        {
            audiotool.play_audioSource(audiotool.LoadAudio(35));
        }
        for (let i = 0; i < data.award.length; i++) {
            if (data.award[i].isWin == true && data.award[i].awardArea != msg.EnumArea.Banker && this.correspond_chips_arr[data.award[i].awardArea].length>0) {
                let quantity = quan_Arr[i];
                //
                for (let k = 0; k < 12; k++) {
                    let area_num = configuration.random(0, 4);
                    this.showCoins(head[0], area, itemarr[area_num], quantity, 0);
                }
            }
        }
        this.timer3 = setTimeout(() => {
            clearTimeout(this.timer3);
            // fun && fun()
            this.fiyWinPos(head, win, fun && fun());
        }, 2 * 1000)
    }
