    public isCompleteFlyChip: boolean = false;
    /**
     * 
     * @param  head 
     * @param  win    
     */
    public fiyWinPos(head: any[], win: number[], fun: Function) {
        let num = Math.ceil(this.allchip_arr.length / win.length);
        let arr = configuration.chunk(this.allchip_arr, num);
        for (let i = 0; i < win.length; i++) {
            if( !cc.isValid( head[win[i]] ) )
            {
                console.log( "fiyWinPos head[win[i]] is null");
                return;
            }
            if (arr[i] == null || arr == null) return;
            else {
                audiotool.play_audioSource(audiotool.LoadAudio(27));
                for (let a = 0; a < arr[i].length; a++) {
                    let postion: cc.Vec2 = head[win[i]].position;   //
                    let r_num = Math.random();
                    if (r_num >= 0.5) {
                        r_num = 1;
                    } else {
                        r_num = 0;
                    }
                    let ac = cc.moveTo(r_num == 0 ? 0.5 : 0.7, postion);
                    let call = cc.callFunc((sender) => {
                        if (a == arr[i].length - 1) fun && fun();
                        if( cc.isValid( sender ) )
                        {
                            sender.destroy();
                            sender = null;
                        }
                    })
                    let seq = cc.sequence(ac, call);
                    if( cc.isValid( arr[i][a]) )
                    {
                        arr[i][a].stopAllActions();
                        arr[i][a].runAction(seq);
                    }
                }
            }
        }
    }
    /**
     * 
     * @param car_node 
     * @param isdisplay 
     * @param fun 
     */
    public directCardValue(car_node: any, isdisplay: boolean, fun): void {
        if ( !cc.isValid( car_node ) ) {
            cc.log('');
            return;
        }
        for (let i = 0; i < this.nodeleng; i++) {
            if( !cc.isValid( car_node[ i ] ) )return;
            var r = car_node[i],
                poke0 = r.getChildByName('poker_0'),
                poke1 = r.getChildByName('poker_1'),
                poke2 = r.getChildByName('poker_2'),
                poke3 = r.getChildByName('poker_3'),
                poke4 = r.getChildByName('poker_4');
            if( !cc.isValid( poke0 ) || !cc.isValid( poke1 ) ||
                !cc.isValid( poke2 ) || !cc.isValid( poke3 ) ||
                !cc.isValid( poke4 ) )
            {
                return;
            }
            poke0.active = isdisplay,
                poke1.active = isdisplay,
                poke2.active = isdisplay,
                poke3.active = isdisplay,
                poke4.active = isdisplay;

            poke0.getChildByName('back').active = false;
            poke1.getChildByName('back').active = false;
            poke2.getChildByName('back').active = false;
            poke3.getChildByName('back').active = false;
            poke4.getChildByName('back').active = false;

            poke0.stopAllActions(),
                poke1.stopAllActions(),
                poke2.stopAllActions(),
                poke3.stopAllActions(),
                poke4.stopAllActions();

            poke0.getPosition(),
                poke1.getPosition(),
                poke2.getPosition(),
                poke3.getPosition(),
                poke4.getPosition();
        }
        var timer = setTimeout(() => {
            clearTimeout(timer)
            fun && fun()
        }, 0 * 1000)
    }
    /** */
    public playCardFly(car_node: any, fun): void {
        if ( !cc.isValid( car_node ) ) {
            cc.log('');
            return;
        }
        for (let i = 0; i < this.nodeleng; i++) {
            if( !cc.isValid( car_node[i] ) )return;
            var r = car_node[i];
            var c = r.parent.convertToWorldSpaceAR(r.parent.position);
            var d = r.convertToNodeSpaceAR(c),
                pos = cc.v2(0, 150),
                poke0 = r.getChildByName('poker_0');

            if( !cc.isValid( poke0 ) )
            {
                return;
            }
            poke0.active = true;

            poke0.stopAllActions();

            let p = poke0.getPosition();
            
            poke0.setPosition((pos.add(d)));
            let call_Audio = cc.callFunc((sender) => {
                audiotool.play_audioSource(audiotool.LoadAudio(14));
            })
            

            
            //
            let call = cc.callFunc((sender) => {
                if (i == this.nodeleng - 1 ) {
                    fun && fun();
                    // cc.log(',flipCard0');
                }
            })
            poke0.runAction(cc.sequence(
                cc.delayTime(0 * .06 + i * .5), call_Audio,
                cc.spawn(cc.moveTo(.15, p),
                    cc.fadeIn(.15),
                    ),
                    call
                ));
        }
    }
