    /**
     * @param  
     */
    showWaiting(n: cc.Node) {
        if( !cc.isValid( n ) )return;
        let d0 = n.getChildByName('dot_0');
        let d1 = n.getChildByName('dot_1');
        let d2 = n.getChildByName('dot_2');

        let action1_show = cc.fadeTo(0.5, 255);
        let action1_hide = cc.fadeTo(0.5, 0);
        let action2_show = cc.fadeTo(0.5, 255);
        let action2_hide = cc.fadeTo(0.5, 0);
        let action3_show = cc.fadeTo(0.5, 255);
        let action3_hide = cc.fadeTo(0.5, 0);

        d0.stopAllActions()
        d1.stopAllActions()
        d2.stopAllActions()
        let callBack1 = cc.callFunc(() => {
            if( cc.isValid( d1 ) )
            {
                d1.runAction(cc.sequence(action2_show, action2_show, callBack2))
            }
        })
        let callBack2 = cc.callFunc(() => {
            if( cc.isValid( d2 ) )
            {
                d2.runAction(cc.sequence(action3_show, action3_show, callBack3))
            }
        })
        let callBack3 = cc.callFunc(() => {
            if( cc.isValid( d2 ) )
            {
                d0.runAction(action1_hide)
                d0.runAction(cc.sequence(action1_show, action1_show, callBack1))
            }
            if( cc.isValid( d1 ) )
            {
                d1.runAction(action2_hide)
            }
            if( cc.isValid( d2 ) )
            {
                d2.runAction(action3_hide)
            }
            
        })
        d0.runAction(cc.sequence(action1_show, action1_show, callBack1))
    }

    /**
     *
     * @param data 
     * @param head 
     */
    public WinPlayerlight(data:msg.GameAward, head: any[]) {
        for (let i = 0; i < data.award.length; i++) {
            let winlose_g = data.offsetMe;
            if ( data.award[i].isWin) { // 
                if( cc.isValid( head[i]))
                {
                    let win_light = head[i].getChildByName('p_win_light');
                    if (winlose_g > 0) {
                        if( cc.isValid( win_light ) )
                        {
                            win_light.active = true;
                            configuration.playAnim(win_light, 0, 'win', false, null);
                        }
                    }
                }
            }
        }
    }

    /**
     *
     * @param data 
     * @param lab_gold_pos gold_lab
     * @param lab_win win_lab 
     * @param lab_lose lose_lab 
     * @param conut (0:1)
     */
    public Floatword(data: msg.GameAward, lab_gold_pos: any[], lab_win: any[], lab_lose: any[], count: number) {
        let index = 0;
        for (let i = 0; i < 9; i=i+7) {
            let winlose:number = null;
            index = i;
            if ( i == 0) {  //
                winlose = data.offsetBanker;
            } else {
                winlose = data.offsetMe;
            }
            if( cc.isValid( lab_gold_pos[index] ) && cc.isValid( lab_win[index] ) && cc.isValid( lab_lose[index] ) )
            {
                let w_pos = lab_gold_pos[index];
                let w = lab_win[index];
                let l = lab_lose[index];
                let w_g = w.getChildByName('win_gold_'),
                    l_g = l.getChildByName('lose_gold_'),
                    winlose_g = winlose,
                    gold_count = configuration.removeDecimalpoint(winlose_g),
                    lab_worl = null;
                w.position = w_pos,
                    l.position = w_pos;
                if (winlose_g > 0) {
                    w.opacity = 255;//
                    l.opacity = 255;
                    w.active = true;
                    l.active = false;
                    lab_worl = w;
                    w_g.getComponent(cc.Label).string = '+' + gold_count;
                } else if (winlose_g < 0) {
                    w.opacity = 255;
                    l.opacity = 255;
                    l.active = true;
                    w.active = false;
                    lab_worl = l;
                    l_g.getComponent(cc.Label).string = '' + gold_count;
                }
                if ( cc.isValid( lab_worl ) ) {
                    lab_worl.stopAllActions();
                    let call = cc.callFunc((sender) => {
                        if( cc.isValid( sender ) )
                        {
                            sender.stopAllActions();
                        }
                    })
                    let seq = cc.moveTo(1, cc.v2(w_pos.x, w_pos.y + 60))
                    let spew = cc.sequence(seq, call);
                    lab_worl.runAction(spew);
                }
            }
        }
    }
    /**
   * 
   * @param data 
   * @param w_num 
   * @param l_num 
   */
    public eatandpay(data:msg.GameAward) {
        let w_num: number = 0;
        let l_num: number = 0;
        for (let i = 0; i < data.award.length; i++) {
            if( data.award[i].awardArea != msg.EnumArea.Banker )
            {
                if (data.award[i].isWin == true) {
                    w_num++;
                } else {
                    l_num++;
                }
            }
        }
        if (w_num == 2) { //
            audiotool.play_audioSource(audiotool.LoadAudio(18));
        }
        if (l_num == 2) {  //
            audiotool.play_audioSource(audiotool.LoadAudio(17));
        }
    }
    public isfilck: boolean = false;
    /**
    * //
    * @param i 
    */
    public blink(i) {
        if( cc.isValid( i ) )
        {
            let l = i;
            l.active = true;
            l.stopAllActions();
            var seq = cc.repeat(
                cc.sequence(
                    cc.fadeTo(0.7, 0),
                    cc.fadeTo(0.7, 255),
                ), 5);
            var call = cc.callFunc((sender) => {
                if( cc.isValid( sender ) )
                {
                    sender.stopAllActions();
                    sender.active = false;
                }
            })
            l.runAction(cc.sequence(seq, call))
        }
    }
    /**
     * 
     * @param t 
     * @param startPoint 
     * @param endPoint 
     * @param height 
     * @param angle 
     */
    public createStarMoveAction(t, startPoint, endPoint, height, angle) {
        // 
        let radian = angle * 3.14159 / 180;
        // 
        let q1x = startPoint.x + (endPoint.x - startPoint.x) / 4;
        let q1 = cc.v2(q1x, height + startPoint.y + Math.cos(radian) * q1x);

        // 
        let q2x = startPoint.x + (endPoint.x - startPoint.x) / 2;
        let q2 = cc.v2(q2x, height + startPoint.y + Math.cos(radian) * q2x);
        // 
        return cc.bezierTo(t, [q1, q2, endPoint]).easing(cc.easeInOut(t));
    }

