import { configuration } from "./brnnConfiguration";
import { audiotool } from "./brnnAudioTool";
import { brnnlanguage } from '../../language/brnnlanguage';
import {msg} from '../brnnproto/brnn_proto_msg';

class brnncreatePoke {

    protected static instance: brnncreatePoke;
    public static getInstance(): brnncreatePoke {
        if (!this.instance) {
            this.instance = new brnncreatePoke();
        }
        return this.instance;
    }
    /**
     * 
     */
    public createPoker(cardpoke, data:msg.GameAward, pokerAtlas) {
        // 
        if (cardpoke == null || cardpoke.length == null) {
            cc.log('');
            return;
        }
        for (let i = 0; i < cardpoke.length; i=i+5) {
            // cardpoke[i].active = true;
            let card_bg = cardpoke[i].getChildByName('card_back');
            let back = cardpoke[i].getChildByName('back');
            let pokenum = cardpoke[i].getChildByName('num');
            let color_0 = cardpoke[i].getChildByName('color_0');
            let color_1 = cardpoke[i].getChildByName('color_1');

            card_bg.stopAllActions();
            back.stopAllActions();
            card_bg.scaleX = 1;
            back.scaleX = 1;
            let pokevalue = data.award[ Math.floor(i / 5)].cards;
            
            var value = pokevalue % 13;
            if (value == 0) {
                value = 13
            }
            var huase = Math.floor((pokevalue) / 13 + 1);
            if (huase == 0) {  //
                huase = 1;
            }
            var num, smallHua, large_Hua;
            // 4,3,2,1
            if (huase == 2 || huase == 4) {
                num = "black_" + value;
                smallHua = "small_" + huase;
                if (value > 10) {        // j,q,k
                    if (huase == 4) {
                        large_Hua = "hua_hei" + value;
                    } else if (huase == 2) {
                        large_Hua = "hua_m" + value;
                    }
                } else {
                    large_Hua = "large_" + huase;
                }
            } else if (huase == 1 || huase == 3) {
                num = "red_" + value;
                smallHua = "small_" + huase;
                if (value > 10) {        // j,q,k
                    if (huase == 3) {
                        large_Hua = "hua_h" + value;
                    } else if (huase == 1) {
                        large_Hua = "hua_f" + value;
                    }
                } else {
                    large_Hua = "large_" + huase;
                }
            }
            pokenum.getComponent(cc.Sprite).spriteFrame = pokerAtlas.getSpriteFrame(num),
                color_0.getComponent(cc.Sprite).spriteFrame = pokerAtlas.getSpriteFrame(smallHua),
                color_1.getComponent(cc.Sprite).spriteFrame = pokerAtlas.getSpriteFrame(large_Hua)
        }
    }

    /**
     *  ()
     */
    public createCardType(carrd_node: any, data: any) {
        if (carrd_node == null || carrd_node.length == null) {
            cc.log('');
            return;
        }
        for (let i = 0; i < carrd_node.length; i++) {
            var cover = carrd_node[i].getChildByName('pai_cover');
            var niu = cover.getChildByName('crad_type').getChildByName('card_niu');
            cover.active = true;
            // niu.active = true;
            configuration.loadSpriteFrame(niu, "brnn/language/" + brnnlanguage.getCurLanguage() + "/cardType/niu_" + data.type[i]);
        }
    }

    /**
    *  ()
    * @param carrd_node 
    * @param datatype data.type[i]
    * @param votgold [0,0,0,0] //
    * @param m //fori  
    */
    public createCardType2(carrd_node: any, datatype: any, votgold: number[], m: number,awardAreaOdds:number) {
        if (carrd_node == null) {
            cc.log('');
            return;
        }
        var cover = carrd_node.getChildByName('pai_cover');
        var niu = cover.getChildByName('crad_type').getChildByName('card_niu');
        var cheng = cover.getChildByName('crad_type').getChildByName('cheng');
        var niu_bet = cover.getChildByName('crad_type').getChildByName('niu_bet');
        carrd_node.getChildByName('poker_3').getChildByName('back').active = false;
        cover.active = true;
        // audiotool.play_audioSource(audiotool.LoadAudio(datatype));
        // niu.active = true;
        // configuration.loadSpriteFrame(niu, "brnn/language/" + brnnlanguage.getCurLanguage() + "/cardType/niu_" + datatype);
        // niu.stopAllActions();
        cheng.stopAllActions();
        niu_bet.stopAllActions();
        // niu.runAction(cc.sequence(cc.scaleTo(0.1, 3, 3), cc.scaleTo(0.08, 2, 2)));
        if (m != 0 && votgold[ m ] != 0 && awardAreaOdds > 0) {
            niu_bet.getComponent(cc.Label).string = awardAreaOdds + '';
            var timer = setTimeout(() => {
                clearTimeout(timer);
                if( !cc.isValid( cheng ) ||
                    !cc.isValid( niu_bet ) )
                {
                    return;
                }
                cheng.active = true;
                niu_bet.active = true;
                cheng.runAction(cc.sequence(cc.scaleTo(0.1, 3.5, 3.5), cc.scaleTo(0.08, 2.5, 2.5))),
                    niu_bet.runAction(cc.sequence(cc.scaleTo(0.1, 1.5, 1.5), cc.scaleTo(0.08, 1, 1)))
            }, 0.3 * 1000)
        }
    }
    /**
     * 
     * @param datatype 
     * @param votgold [0,0,0,0] //
     * @param datatype == 10  
     * @param datatype == 11  
     * @param datatype== 12  
     */
    public cardTypeBet(datatype: any, niu_bet: cc.Node) {
        if (datatype >= 0 && datatype <= 1) {
            niu_bet.getComponent(cc.Label).string = 1 + '';
        } else if (datatype >= 2 && datatype <= 9) {
            niu_bet.getComponent(cc.Label).string = datatype + '';
        } else if (datatype >= 10) {
            niu_bet.getComponent(cc.Label).string = 10 + '';
        }
    }
    /**
     * 
     * @param W_gold_lab lab
     * @param l_gold_lab lab
     */
    public myPlayerWLGold(W_gold_lab: cc.Node[], l_gold_lab: cc.Node[], data: any) {
        cc.log(' ');
        if (data == null || data.bet_result == null) return;
        for (let i = 0; i < W_gold_lab.length; i++) {
            if (data.bet_result[i] > 0) {
                if( cc.isValid( W_gold_lab[i] ) )
                {
                    W_gold_lab[i].active = true;
                    W_gold_lab[i].getComponent(cc.Label).string = data.bet_result[i] + '';
                }
            } else if (data.bet_result[i] < 0) {
                if( cc.isValid( l_gold_lab[i] ) )
                {
                    l_gold_lab[i].active = true;
                    l_gold_lab[i].getComponent(cc.Label).string = data.bet_result[i] + '';
                }
            }
        }
    }
    /**
     * 
     * @param chiptype type=value0=1,1=10,2=50,3=100,4=1000
     */
    public JudgeChipValue(chiptype: number) {
        let value = 0;
        switch (chiptype) {
            case 0:
                value = 1;
                break;
            case 1:
                value = 10;
                break;
            case 2:
                value = 50;
                break;
            case 3:
                value = 100;
                break;
            case 4:
                value = 1000;
                break;
            default:
                break;
        }
        return value;
    }
    /**
     *
     * @param mybetNum 
     */
    public DiffentChipArea(mybetNum: number) {
        if (mybetNum < 0) {
            return;
        }
        let num = mybetNum;
        let count: number[] = [0, 0, 0, 0, 0];
        if (num >= 1000) {
            count[4] = Math.floor(num / 1000);
            num = num % 1000;
        }
        if (num >= 100) {
            count[3] = Math.floor(num / 100);
            num = num % 100;
        }
        if (num >= 50) {
            count[2] = Math.floor(num / 50);
            num = num % 50;
        }
        if (num >= 10) {
            count[1] = Math.floor(num / 10);
            num = num % 10;
        }
        if (num >= 1) {
            count[0] = Math.floor(num / 1);
            num = num % 1;
        }
        return count;
    }

}
export const createpoke = brnncreatePoke.getInstance();
