    private initdata(): void {
        this.spin_kaishi.active = false;
        this.spin_tingzhi.active = false;

        this.tipstate_qxz.active = false;
        this.tipstate_qdd.active = false;

        for (let i = 0; i < this.xinxin.length; i++) {
            if( cc.isValid( this.xinxin[i] ) )
            {
                this.xinxin[i].active = false;
            }
        }
        for (let i = 0; i < this.myw_gold_lab.length; i++) {
            if( cc.isValid( this.myw_gold_lab[i] ) )
            {
                this.myw_gold_lab[i].active = false;
            }
            if( cc.isValid( this.myl_gold_lab[i] ))
            {
                this.myl_gold_lab[i].active = false;
            }
        }
        for (let i = 0; i < 4; i++) {
            if( cc.isValid( this.twink[i] ) )
            {
                this.twink[i].active = false;
            }
            if( cc.isValid( this.tableNum[i] ) )
            {
                this.tableNum[i].getComponent(cc.Label).string = "0";
                this.tableNum[i].active = false;
            }
            if( cc.isValid( this.p_coin[i] ) )
            {
                this.p_coin[i].active = false;
            }
            if( cc.isValid( this.vot[i] ))
            {
                this.vot[i].active = false;
            }
            if( cc.isValid( this.di_bg[i] ))
            {
                this.di_bg[i].active = false;
            }
        }
        for (let i = 0; i < this.playerHead.length - 2; i++) {
            if( cc.isValid( this.playerHead[i] ) )
            {
                if( cc.isValid( this.playerHead[i].getChildByName('p_win_light') ))
                {
                    this.playerHead[i].getChildByName('p_win_light').active = false
                }
            }
        }
        for (let i = 0; i < this.cardpoke.length; i++) {
            if( cc.isValid( this.cardpoke[i] ))
            {
                this.cardpoke[i].active = false;
                this.cardpoke[i].getChildByName('back').active = true;
            }
        }

        for (let k = 0; k < 5; k++) {
            if( cc.isValid( this.carrd_node[k] ) )
            {
                let cover = this.carrd_node[k].getChildByName('pai_cover');
                cover.active = false;
                cover.getChildByName('crad_type').getChildByName('cheng').active = false;
                cover.getChildByName('crad_type').getChildByName('niu_bet').active = false;
            }
        }
        if( cc.isValid( this.waitsp ) )
        {
            this.waitsp.active = false;
        }

    }
    /** */
    private initSzList() {
        this.updatePreBankList(configuration.PrePokerList);
    }
    onLoad() {
        audiotool.loadSoundsRes( this.node );
        this.isone = 0;
        this.init();
        this.initdata();
        this.addBankerInfo(configuration.JoinRoomR);
        this.addPlayerInfo();
        // this.JudgeGametype();
        // this.judgeIsPlay();
        this.addOrdinaryEventListener();
        this.addInterEventListener();

        websocket.init();
        websocket.setCurSceneRootNode( this.node );
        brnnutils.setMessageBoxPrefab( this.Messeagebox );
        this.whetherPlayAudio();
    }
    start() {
        this.gameProcess();
        
        //  
        if( this.roomstate == 1 )
        {
            this.votgold = configuration.JoinRoomR.mybet_list;
            for (let i = 0; i < configuration.JoinRoomR.mybet_list.length; i++) {
                this.eachtypevotcount[i] = createpoke.DiffentChipArea(configuration.JoinRoomR.mybet_list[i]);
            }
        }
        cc.game.on(cc.game.EVENT_HIDE, this.gameHideBackgroundAction.bind( this ), this);
        cc.game.on(cc.game.EVENT_SHOW, this.gameShowAction.bind( this), this);

        this.whetherPlayAudio();

        this.scheduleOnce( ()=>{
            let tempnode = cc.find( "UI/brnn_bg" , this.node );
            if( cc.isValid( tempnode ) )
            {
                let tempWidget = tempnode.getComponent( cc.Widget );
                if( cc.isValid( tempWidget ) )
                {
                    tempWidget.alignMode = cc.Widget.AlignMode.ON_WINDOW_RESIZE;
                }
            }

            tempnode = cc.find( "UI/bottom_layer/p_bottom" , this.node );
            if( cc.isValid( tempnode ) )
            {
                let tempWidget = tempnode.getComponent( cc.Widget );
                if( cc.isValid( tempWidget ) )
                {
                    tempWidget.alignMode = cc.Widget.AlignMode.ON_WINDOW_RESIZE;
                }
            }

            if( cc.isValid( this.btn_back ) )
            {
                this.btn_back.getComponent(cc.Button).interactable = true;
            }
        },1)
    }
