        events.register(EventKind.S2C_PreBanker, "brnnGameScene", this.prePokerErr.bind(this));//
        events.register(EventKind.S2C_PreBankerList, "brnnGameScene", this.prePokerList.bind(this));//
        // events.register(EventKind.S2C_PlayerBet, "brnnGameScene", this.PlayerBet2.bind(this));//
        events.register(EventKind.S2C_OpenLoterry, "brnnGameScene", this.LotteryResult.bind(this));//
        events.register(EventKind.S2C_ShowPlayerList, "brnnGameScene", this.storageT_p.bind(this));//
        events.register(EventKind.S2C_ExitPreBanker, "brnnGameScene", this.cancalApply.bind(this));//
        events.register(EventKind.S2C_mbankerChange, "brnnGameScene", this.storageCb.bind(this));//
        events.register(EventKind.S2C_SzModifgold, "brnnGameScene", this.changeMyGold.bind(this));
