        events.register(EventKind.S2CEnterRoom, "brnnGameScene", this.renewJionRoom.bind(this));//
        events.register(EventKind.S2C_RenewPlayerList, "brnnGameScene", this.storageT_p.bind(this));//
        events.register(EventKind.S2C_RenewpreBankerList, "brnnGameScene", this.prePokerList.bind(this));//

