    /** */
    private judgeIsPlay() {
        let type = 'rongyao';
        this.type = type;
        brnnglobal.roomname = configuration.JoinRoomR.name;
        cc.log('', brnnglobal.roomname , "configuration.UserInfo.gold=",configuration.UserInfo.gold);
        if (type == 'rongyao') {
            if (configuration.UserInfo.gold < 30) {

                if( cc.isValid( this.spin_watch ) )
                {
                    this.spin_watch.active = true;
                }
                if( cc.isValid( this.wachtip ) )
                {
                    this.wachtip.active = true;
                    this.wachtip.getComponent(cc.Label).string = brnnlanguage.getTextByID(10) + 30 + brnnlanguage.getTextByID(31);
                }
                this.wicthingAnim();
                brnnglobal.isxz = false;
                brnnglobal.enoughgold = false;
            } else {
                if( cc.isValid( this.wachtip ) )
                {
                    this.wachtip.active = false;
                }

                if( cc.isValid( this.spin_watch ) )
                {
                    this.spin_watch.active = false;
                }
                brnnglobal.enoughgold = true;
            }
            this.roomTypeBet = 5;
            configuration.loadSpriteFrame(this.beichang, "brnn/language/" + brnnlanguage.getCurLanguage() + '/10beichang');
        } else if (type == 'chuanqi') {
            if (configuration.UserInfo.gold < 50) {
                
                if( cc.isValid( this.spin_watch ) )
                {
                    this.spin_watch.active = true;
                }
                if( cc.isValid( this.wachtip ) )
                {
                    this.wachtip.active = true;
                    this.wachtip.getComponent(cc.Label).string = brnnlanguage.getTextByID(10) + 50 + brnnlanguage.getTextByID(31);
                }
                this.wicthingAnim();
                brnnglobal.isxz = false;
                brnnglobal.enoughgold = false;
            } else {
                if( cc.isValid( this.wachtip ) )
                {
                    this.wachtip.active = false;
                }

                if( cc.isValid( this.spin_watch ) )
                {
                    this.spin_watch.active = false;
                }
                brnnglobal.enoughgold = true;
            }
            this.roomTypeBet = 10;
            configuration.loadSpriteFrame(this.beichang, "brnn/language/" + brnnlanguage.getCurLanguage() + '/10beichang');
        }
    }
