    /**
     * @param index 0: 1:
     */
    private displayChipCardValue(data: any) {
        if (this.time_gameState > 0 && this.time_gameState < 11) {
            for (let i = 0; i < 4; i++) {//
                let quantity = cc.find('Canvas/UIlayer/quantity_' + i);//:0 :1 :2 :3
                if (configuration.JoinRoomR.bet_list[i] != 0) {
                    if( !cc.isValid( this.tableNum[i] ) )return;
                    this.tableNum[i].active = true;
                    let gameview = this.node.getComponent('brnnGameView');
                    let headPos = gameview.initBetRegionPos[i];
                    this.tableNum[i].getComponent(cc.Label).string = configuration.JoinRoomR.bet_list[i] + '';
                    brnnanimate.points_chip_move(configuration.JoinRoomR.bet_list[i], headPos, this.Grp_cz, quantity, this.chip, i);
                }
                if (configuration.JoinRoomR.mybet_list[i] != 0) {
                    if( !cc.isValid( this.vot[i] ) || !cc.isValid( this.p_coin[i] ) ||
                        !cc.isValid( this.di_bg[i] ) )
                    {
                        return;
                    }
                    this.vot[i].active = true;
                    this.p_coin[i].active = true;
                    this.di_bg[i].active = true;
                    this.vot[i].getComponent(cc.Label).string = configuration.JoinRoomR.mybet_list[i] + '';
                }
            }
            //
            brnnanimate.directCardValue(this.carrd_node, true,
                this.setCardValue(data,
                    createpoke.createCardType(this.carrd_node, data)))
        }
    }

    /***/
    private updateSettleInfo(data: any) {
        this.s_timer0 = this.scheduleOnce( ()=> {
            this.TableflickerAnim(data);
            this.recordWinner(data);
            brnnanimate.Chipset(this.playerHead, this.winplayer, data, this.quanti, this.Grp_cz, this.chip, null);
        }, 9 - this.time_gameState)
        if (this.time_gameState < 13) {
            this.s_timer1 = this.scheduleOnce( ()=> {
                // this.recordWinner(data);
                // brnnanimate.Chipset(this.playerHead, this.winplayer, data, this.quanti, this.Grp_cz, this.chip, null);
                this.Updategold(data);
                brnnanimate.Floatword(data, this.winlose_gold_pos, this.win_gold, this.lose_gold, 1);
                this.addW_LPanl(data);
                createpoke.myPlayerWLGold(this.myw_gold_lab, this.myl_gold_lab, data);
                brnnanimate.WinPlayerlight(data, this.playerHead);
            }, 11 - this.time_gameState)
        }
        this.s_timer2 = this.scheduleOnce( ()=> {
            // this.Updategold(data);
            // brnnanimate.Floatword(data, this.winlose_gold_pos, this.win_gold, this.lose_gold, 1);
            // createpoke.myPlayerWLGold(this.myw_gold_lab, this.myl_gold_lab, data);
            // this.addW_LPanl(data);
        }, 13 - this.time_gameState)

    }
    /** */
    private bankerChange() {
        if (brnnglobal.isChangbanker == true) {
            brnnglobal.isChangbanker = false;
            audiotool.play_audioSource(audiotool.LoadAudio(23));
            this.addBankerInfo(configuration.ChangeBanker);//
            let pre_bank_id = configuration.ChangeBanker.pre_banker.id;
            if( !cc.isValid( this.myhead ) || !cc.isValid( this.btn_applysz ) ||
                    !cc.isValid( this.btn_cancelapply ) || !cc.isValid( this.btn_applyxz ) ||
                    !cc.isValid( this.btn_sz ) || !cc.isValid( this.btn_qx ) ||
                    !cc.isValid( this.btn_xz ) || !cc.isValid( this.banker_tip ))
            {
                return;
            }
            if (pre_bank_id == configuration.UserInfo.id) {//
                let my_gold = this.myhead.getChildByName('lab_info_glod');
                let gold_count = configuration.removeDecimalpoint(configuration.ChangeBanker.pre_banker.ugold);
                my_gold.getComponent(cc.Label).string = gold_count + '';
                configuration.UserInfo.gold = configuration.ChangeBanker.pre_banker.ugold;
                this.my_sz_gold = 0;
                this.btn_applysz.active = true;
                this.btn_cancelapply.active = false;
                this.btn_applyxz.active = false;
                this.btn_sz.active = true;
                this.btn_qx.active = false;
                this.btn_xz.active = false;
            }
            let banker_img = this.banker_tip.getChildByName('img_head');
            this.getPlayerImg(configuration.ChangeBanker.banker.headurl, banker_img, this.banker_tip, true);
            this.banker_tip.active = true;
            this.banker_tip.runAction( cc.sequence( cc.scaleTo(0.5, 1, 1) ,
                                                    cc.delayTime( 2 ),
                                                    cc.callFunc( ()=>{
                                                        if( cc.isValid( this.banker_tip ) )
                                                        {
                                                            this.banker_tip.active = false;
                                                        }
                                                    })
            ));
            let banker_id = configuration.ChangeBanker.banker.id;
            if (banker_id == configuration.UserInfo.id) { // 
                this.btn_applysz.active = false;
                this.btn_cancelapply.active = false;
                this.btn_applyxz.active = true;

                this.btn_sz.active = false;
                this.btn_qx.active = false;
                this.btn_xz.active = true;
                this.my_sz_gold = configuration.ChangeBanker.banker.lgold;
            }
        }
    }
