    private zb_callback: any = null;
    /** */
    private startCountDown0(time: number, timer_node) {
        this.unschedule(this.zb_callback);
        this.unschedule(this.xz_callback);
        this.zb_callback = ()=>{
            if( !cc.isValid( timer_node ) )
            {
                this.unschedule(this.zb_callback);
                this.unschedule(this.xz_callback);
                return;
            }
            if (time <= 1) {// time=0
                timer_node.getComponent(cc.Label).string = '';
                this.unschedule(this.zb_callback);
            }
            time--;
            // cc.log('', time);
            timer_node.getComponent(cc.Label).string = time + '';
        }
        this.schedule(this.zb_callback, 1, cc.macro.REPEAT_FOREVER, 0.01);//
    }
    private xz_callback: any = null;
    /** */
    private startCountDown1(time: number, timer_node) {
        this.unschedule(this.zb_callback);
        this.unschedule(this.xz_callback);
        this.xz_callback = ()=>{
            if( !cc.isValid( timer_node ) )
            {
                this.unschedule(this.zb_callback);
                this.unschedule(this.xz_callback);
                return;
            }
            if (time <= 6 && time > 1) {
                audiotool.play_audioSource(audiotool.LoadAudio(28));
            }
            if (time <= 1) {   // time=0
                audiotool.play_audioSource(audiotool.LoadAudio(31));
                timer_node.getComponent(cc.Label).string = '';
                brnnglobal.isxz = false;
                // this.btn_continvot.getComponent(cc.Button).interactable = false;
                // this.xutou_numbg.active = false;
                for (let i = 0; i < 5; i++) {
                    if( cc.isValid( this.grp_chip_btn[i] ))
                    {
                        this.grp_chip_btn[i].interactable = false;
                        this.grp_chip_btn[i].node.getChildByName('p_chip_circle').active = false;
                    }
                }
                this.unschedule(this.xz_callback);
                this.StopBetAnim();
            }
            time--;
            // cc.log('', time);
            timer_node.getComponent(cc.Label).string = time + '';

        }
        this.schedule(this.xz_callback, 1, cc.macro.REPEAT_FOREVER, 0.01);
    }

