            let playerHead = cc.find('Canvas/Centerlayer/head_player_7');
            let table_gold = playerHead.getChildByName('gold');//
            let gold_count = configuration.removeDecimalpoint(configuration.UserInfo.gold);
            gold.getComponent(cc.Label).string = gold_count + '';
