    /** */
    private saveVotData() {
        if( !cc.isValid( this.node ) )return;
        
        for (let i = 0; i < this.eachtypevotcount.length; i++) {
            if (this.votgold[i] != 0) {
                this.votgold1 = this.votgold;
                break;
            }
        }
        this.mytotalvot = 0;
        for (let i = 0; i < this.votgold1.length; i++) {
            this.mytotalvot += this.votgold1[i];
        }
        var needbreak = false
        for (let i = 0; i < this.eachtypevotcount.length; i++) {
            for (let j = 0; j < this.eachtypevotcount[i].length; j++) {
                for (let m = 0; m < this.eachtypevotcount[i][j]; m++) {
                    if (this.eachtypevotcount[i][j] != 0) {
                        this.eachtypevot = this.eachtypevotcount;
                        needbreak = true
                        break;
                    }
                }
                if (needbreak) {
                    break;
                }
            }
            if (needbreak) {
                break;
            }
        }

    }
    /** */
    private LotteryResult(xdata: any) {
        this.saveVotData();
        if (xdata == null) {
            return;
        }

        if( !cc.isValid( this.xutou_numbg ) ||
            !cc.isValid( this.tipstate_qxz ) )
        {
            return;
        }
        if( xdata.robot_num >= 40 )
        {
            this.playbetAudioMax = 3;
        }
        else
        {
            this.playbetAudioMax = 0;
        }
        this.playbetAudioCount = this.playbetAudioMax;

        brnnglobal.isRoundBeenBet = false;
        brnnglobal.isxz = false;
        this.isSettle = true;
        let mdata = JSON.parse(xdata.data);
        this.btn_continvot.getComponent(cc.Button).interactable = false;
        this.xutou_numbg.active = false;
        this.iscontinue = mdata.continue;
        this.tipstate_qxz.active = false;
        for (let i = 0; i < mdata.player_list.length; i++) {
            if (mdata.player_list[i].id == configuration.UserInfo.id) {
                configuration.UserInfo.gold = mdata.player_list[i].ugold;
            }
        }
        brnnanimate.playCardFly(this.carrd_node, ()=> {
            this.setCardValue(mdata, null);
            brnnanimate.flipCard0(this.carrd_node, 0, ()=> {
                brnnanimate.flipCard1(this.carrd_node, mdata, this.votgold, ()=> {
                    this.addWinRate(mdata);
                    this.beyondWlrate();
                    this.TableflickerAnim(mdata);
                    brnnanimate.eatandpay(mdata);
                })
            })
        });
        this.s_timer5 = this.scheduleOnce(()=> {
            this.recordWinner(mdata);
            brnnanimate.Chipset(this.playerHead, this.winplayer, mdata, this.quanti, this.Grp_cz, this.chip, null);
        }, 9)
        this.s_timer6 = this.scheduleOnce(()=> {
            // this.recordWinner(mdata);
            // brnnanimate.Chipset(this.playerHead, this.winplayer, mdata, this.quanti, this.Grp_cz, this.chip, null);
            brnnanimate.Floatword(mdata, this.winlose_gold_pos, this.win_gold, this.lose_gold, 0);
            brnnanimate.WinPlayerlight(mdata, this.playerHead);
            this.Updategold(mdata);
            this.addW_LPanl(mdata);
            createpoke.myPlayerWLGold(this.myw_gold_lab, this.myl_gold_lab, mdata);
        }, 11)
        this.s_timer7 = this.scheduleOnce(()=> {
            // brnnanimate.Floatword(mdata, this.winlose_gold_pos, this.win_gold, this.lose_gold, 0);
            // brnnanimate.WinPlayerlight(mdata, this.playerHead);
            // this.Updategold(mdata);
            // createpoke.myPlayerWLGold(this.myw_gold_lab, this.myl_gold_lab, mdata);
            // this.addW_LPanl(mdata);
        }, 13)
        this.s_timer8 = this.scheduleOnce(()=> {
            this.restGame();
            this.roomstate = 0; // roostate0
            this.time_gameState = 16;
            this.gameProcess();
        }, 15)

        if( cc.isValid( this.jiazaiAnim ))
        {
            this.jiazaiAnim.active = false;
        }
    }
