    private winplayer: number[] = [];
    private recordWinner(data: any) {
        for (let i = 0; i < data.player_list.length; i++) {
            let winlose_g = data.player_list[i].winlose;
            if (winlose_g > 0) {     //
                if (data.player_list[i].seat == -1) {
                    this.winplayer.push(0);
                } else if (data.player_list[i].seat == 0) {
                    if (data.player_list[i].id == configuration.UserInfo.id) {
                        this.winplayer.push(7);
                    } else {
                        this.winplayer.push(8);
                    }
                } else {
                    if (data.player_list[i].id == configuration.UserInfo.id) {
                        this.winplayer.push(7);
                    } else {
                        this.winplayer.push(data.player_list[i].seat);
                    }
                }
            }
        }
    }
    /** */
    private Updategold(data:msg.GameAward) {
        //
        let u_gold_count = configuration.removeDecimalpoint(configuration.UserInfo.gold);
        let my_gold = this.myhead.getChildByName('lab_info_glod');
        my_gold.getComponent(cc.Label).string = u_gold_count + '';
    }
    /**  */
    private addW_LPanl(data: any) {
        for (let i = 0; i < data.player_list.length; i++) {
            if (data.player_list[i].id == configuration.UserInfo.id) {//
                if (data.player_list[i].winlose > 0) {
                    audiotool.play_audioSource(audiotool.LoadAudio(16));
                    let win_panl = cc.instantiate(this.Winpanl);
                    let brnnWinPanl = win_panl.getComponent('brnnWinPanl');
                    brnnWinPanl.settleMentWin(data, i);
                    this.wl_start_stop_anim.addChild(win_panl);
                } else if (data.player_list[i].winlose < 0) {
                    audiotool.play_audioSource(audiotool.LoadAudio(15));
                    let lose_panl = cc.instantiate(this.Losepanl);
                    let brnnLosePanl = lose_panl.getComponent('brnnLosePanl');
                    brnnLosePanl.settleMentLose(data, i);
                    this.wl_start_stop_anim.addChild(lose_panl);
                }
            }
        }
    }
    /** */
    private addWinRate(data: any) {
        var self = this;
        if( !cc.isValid( this.rare_scrollw ) ||
            !cc.isValid( this.winlos_node ) ||
            !cc.isValid( this.ratePanl ) ||
            !cc.isValid( this.biao_mid1 ) )
        {
            return;
        }
        this.rare_scrollw.getComponent(cc.ScrollView).content.position = this.initRateContentPos;
        for (let i = 0; i < data.result.result.length; i++) {
            let wlitem = cc.instantiate(this.winlost);
            if (data.result.result[i] == true) {
                configuration.loadSpriteFrame(wlitem, "brnn/brnnlist/sheng_1");
            } else if (data.result.result[i] == false) {
                configuration.loadSpriteFrame(wlitem, "brnn/brnnlist/fu_1");
            }
            this.winlos_node.addChild(wlitem);
            this.winlos_node.getComponent(cc.Layout).updateLayout();
        }
        this.allwinratearr.push(data.result.result);
        this.getRate();
        if (this.ischecked) {
            this.ratePanl.active = true;
            this.ratePanl.stopAllActions();
            this.ratePanl.runAction(
                cc.moveTo(0.5, cc.v2(0, 0))
            );
            this.biao_mid1.active = true;
        }
        brnnanimate.blink(this.biao_mid1);
    }
    /**21 */
    private beyondWlrate() {
        if (this.allwinratearr.length > 21) {
            for (let r = 0; r < 4; r++) {
                if( cc.isValid( this.winlos_node.children[r] ) )
                {
                    this.winlos_node.children[r].destroy();
                    // this.winlos_node.children[r] = null;
                }
            }
            this.allwinratearr.splice(0, 1);
        }
    }
