    /** */
    private prePokerList(xdata: any) {
        if (!xdata) return;
        let mdata = JSON.parse(xdata.data);
        if (!mdata) return;
        this.updatePreBankList(mdata);
        configuration.initPrePokerList(xdata);
        if (mdata.id != '' && mdata.id == configuration.UserInfo.id) {
            let hasme = false;
            if( configuration.PrePokerList.banker_list instanceof Array )
            {
                for( let bankListIndex = 0;bankListIndex < configuration.PrePokerList.banker_list.length; bankListIndex++ )
                {
                    if( configuration.PrePokerList.banker_list[ bankListIndex ].id == configuration.UserInfo.id )
                    {
                        hasme = true;
                        break;
                    }
                }
            }

            if ( hasme == false ) {
                //
                this.iscancel = true;
                if( cc.isValid( this.btn_sz ) &&
                    cc.isValid( this.btn_qx ) &&
                    cc.isValid( this.btn_xz ) )
                {
                    this.btn_sz.active = true;
                    this.btn_qx.active = false;
                    this.btn_xz.active = false;
                }
            } else {
                //
                this.isApplySz = true;
                if( cc.isValid( this.btn_sz ) &&
                    cc.isValid( this.btn_qx ) &&
                    cc.isValid( this.btn_xz ) )
                {
                    this.btn_sz.active = false;
                    this.btn_qx.active = true;
                    this.btn_xz.active = false;
                }
            }
            this.errTipPrefab('');
            if( cc.isValid( this.jiazaiAnim ))
            {
                this.jiazaiAnim.active = false;
            }
        }
    }
    private updatePreBankList(mdata: any) {
        return;
        let content = cc.find('Canvas/Centerlayer/prepoker/perPoke_ScrollView/view/content');
        if( !cc.isValid( content ) )return;
        content.height = 0;
        if (content.childrenCount > 0) {
            for (let i = 0; i < content.childrenCount; i++) {
                content.removeAllChildren();
            }
        }

        let sz_Scrollow = this.SzPanl.getChildByName('Sz_ScrollView');
        if( !cc.isValid( sz_Scrollow ) )return;
        let sz_content = sz_Scrollow.getComponent(cc.ScrollView).content;
        if( !cc.isValid( sz_content ) )return;
        sz_content.height = 0;
        if (sz_content.childrenCount > 0) {
            for (let i = 0; i < sz_content.childrenCount; i++) {
                sz_content.removeAllChildren();
            }
        }

        if( cc.isValid( this.prepoker_num ) )
        {
            /** */
            if (mdata.banker_list == null) {
                this.prepoker_num.getComponent(cc.Label).string = 0 + '';
                return;
            } else {
                cc.log('', mdata.banker_list.length);
                this.prepoker_num.getComponent(cc.Label).string = mdata.banker_list.length + '';
            }
        }

        if( cc.isValid( this.perPokeScrollow ) )
        {
            if( this.perPokeScrollow.getComponent(cc.ScrollView) )
            {
                if( this.perPokeScrollow.getComponent(cc.ScrollView).content )
                {
                    for (let i = 0; i < mdata.banker_list.length; i++) {
                        content.height += 21;
                        let item = cc.instantiate(this.perlist);
                        this.perPokeScrollow.getComponent(cc.ScrollView).content.addChild(item);
                        item.getComponent(cc.Label).string = mdata.banker_list[i].name + '';
                    }
                }
            }
        }
        
        /** */

        for (let i = 0; i < mdata.banker_list.length; i++) {
            sz_content.height += 60;
            let sz_item = cc.instantiate(this.sz_item);
            let sz_count = sz_item.getChildByName('sz_count');
            let sz_name = sz_item.getChildByName('sz_name');
            let sz_gold = sz_item.getChildByName('p_kuang').getChildByName('sz_gold');
            sz_count.getComponent(cc.Label).string = i + 1 + '.';
            sz_name.getComponent(cc.Label).string = mdata.banker_list[i].name + '';
            sz_gold.getComponent(cc.Label).string = mdata.banker_list[i].lgold + '.00';
            sz_content.addChild(sz_item);

            if (mdata.banker_list[i].id == configuration.UserInfo.id) {
                if( cc.isValid( this.btn_applysz ) &&
                    cc.isValid( this.btn_applyxz ) &&
                    cc.isValid( this.btn_cancelapply ) &&
                    cc.isValid( this.btn_sz ) &&
                    cc.isValid( this.btn_xz ) &&
                    cc.isValid( this.btn_qx ) )
                {
                    this.btn_applysz.active = false;
                    this.btn_applyxz.active = false;
                    this.btn_cancelapply.active = true;
                    this.btn_sz.active = false;
                    this.btn_xz.active = false;
                    this.btn_qx.active = true;
                }
            }
        }
    }
