    /**
     * @param data
     * @param img img
     */
    private getPlayerImg(data: any, img: cc.Node, head: cc.Node, isbankerchange: boolean) {
        let img_num = '';
        if (data.length == 6) {
            img_num = data.substr(0, 2);
        } else if (data.length == 5) {
            img_num = data.substr(0, 1);
        }
        if (parseInt(img_num) >= 10)
            img_num = parseInt(img_num) % 10 + '';
        if (isbankerchange == true)
            configuration.loadSpriteAtlasFrame(img, 'brnn/brnnhead/im_thead', 'tAvatar' + img_num);
        else
            configuration.loadSpriteAtlasFrame(img, 'brnn/brnnhead/im_head', 'Avatar' + img_num);
    }
    private allwinratearr: any[] = [];
    /* */
    private getInitWinRate() {
        var self = this;
        if (configuration.ListHistory.data.length == null) return;
        let roomname = configuration.JoinRoomR.name;
        for (let i = 0; i < configuration.ListHistory.data.length; i++) {
            let name = configuration.ListHistory.data[i].name;
            let history = configuration.ListHistory.data[i].history;
            if (name == roomname && history.length != 0) {
                let leng = 0;
                if (history.length > 21) {   //21(21)
                    leng = history.length - 21;
                } else {
                    leng = 0;
                }
                for (let j = leng; j < history.length; j++) {
                    for (let k = 0; k < history[j].length; k++) {
                        let wlitem = cc.instantiate(this.winlost);
                        if (history[j][k] == true) {
                            configuration.loadSpriteFrame(wlitem, "brnn/brnnlist/sheng_1");
                        } else if (history[j][k] == false) {
                            configuration.loadSpriteFrame(wlitem, "brnn/brnnlist/fu_1");
                        }
                        this.winlos_node.addChild(wlitem);
                    }
                    this.allwinratearr.push(history[j]);
                }
            }
            if (roomname == 'rongyao1') {
                this.roomtype.getComponent(cc.Label).string = '01';
            } else if (roomname == 'rongyao2') {
                this.roomtype.getComponent(cc.Label).string = '02';
            } else if (roomname == 'chuanqi1') {
                this.roomtype.getComponent(cc.Label).string = '01';
            } else if (roomname == 'chuanqi2') {
                this.roomtype.getComponent(cc.Label).string = '02';
            }
        }
        this.beyondWlrate();
        this.getRate();
    }
