    private num: number[] = [0, 0, 0, 0];
    /**20 */
    private getRate() {
        return;
        this.num = [0, 0, 0, 0];
        for (let j = this.allwinratearr.length < 21 ? 0 : 1; j < this.allwinratearr.length; j++) {
            for (let m = 0; m < 4; m++) {
                if (this.allwinratearr[j][m] == true) {
                    this.num[m]++;
                }
            }
        }
        this.lab_winrate0.getComponent(cc.Label).string = this.num[0] + '/20';
        this.lab_winrate1.getComponent(cc.Label).string = this.num[1] + '/20';
        this.lab_winrate2.getComponent(cc.Label).string = this.num[2] + '/20';
        this.lab_winrate3.getComponent(cc.Label).string = this.num[3] + '/20';
    }
