        this.chipRecordIndex = 0;
        this.isSwitchScreen = false;
        this.isclickBtn_back = false;
        this.isclicki_Btn_enter = false;
        cc.audioEngine.stopAll();
        this.node.stopAllActions();
        this.removeOrdinaryEventListener();
        this.removeInterEventListener();
        this.removeDelayTimer();
        cc.game.off(cc.game.EVENT_HIDE, ()=> {
            this.gameHideBackgroundAction();
        }, self);
        cc.game.off(cc.game.EVENT_SHOW, ()=> {
            this.gameShowAction();
        }, self);

        this.type = '';
        this.my_sz_gold = 0;
        this.isprepoker = true;
        this.df_sc_y = -1;
        this.roomTypeBet = 0;
        this.initRateContentPos = cc.v2(0, 0);
