    S2C_GameStart(data:msg.GameStart)
    {
        if( cc.isValid( this.jiazaiAnim ))
        {
            this.jiazaiAnim.active = false;
        }

        this.roomstate = 0; // roostate0
        this.time_gameState = data.readyTimer;

        this.restGame();
        this.judgeIsPlay();
        this.addPlayerInfo();
        this.PreparPhaseDisplay();
    }

    S2C_GameStartBet(data:msg.GameStartBet)
    {
        if( cc.isValid( this.jiazaiAnim ))
        {
            this.jiazaiAnim.active = false;
        }
        this.roomstate = 1; // roostate1
        this.time_gameState = data.readyTimer;
        events.register(EventKind.S2C_PlayerBet, "brnnGameScene", this.PlayerBet2.bind(this));//            
        this.BetStageDisplay();
        this.judgeIsPlay();
        this.JudgeContinuedVot();
        this.tipstate_qdd.active = false;
        this.tipstate_qxz.active = true;
        this.startCountDown1(this.time_gameState, this._timer_lab_qxz);
        configuration.loadSpriteFrame(this._state, "brnn/language/" + brnnlanguage.getCurLanguage() + '/p_xiazhu');
    }

    S2C_GameAward(data:msg.GameAward)
    {
        if( cc.isValid( this.jiazaiAnim ))
        {
            this.jiazaiAnim.active = false;
        }

        this.roomstate = 2; // roostate0
        this.time_gameState = data.readyTimer;

        this.saveVotData();

        brnnglobal.isRoundBeenBet = false;
        brnnglobal.isxz = false;
        this.isSettle = true;
        // this.btn_continvot.getComponent(cc.Button).interactable = false;
        // this.xutou_numbg.active = false;
        this.iscontinue = 0;
        this.tipstate_qxz.active = false;
        configuration.UserInfo.gold = data.balance;

        brnnanimate.playCardFly(this.carrd_node, ()=> {
            this.setCardValue(data, null);
            brnnanimate.flipCard0(this.carrd_node, 0, ()=> {
                brnnanimate.flipCard1(this.carrd_node, data, this.votgold, ()=> {
                    this.TableflickerAnim(data);
                    brnnanimate.eatandpay(data);
                })
            })
        });
        this.s_timer5 = this.scheduleOnce(()=> {
            for (let i = 0; i < data.award.length; i++) {
                if ( data.award[i].isWin == true && data.award[i].awardArea != msg.EnumArea.Banker ) {
                    this.winplayer.push(7);
                    break;
                }
            }
            
            brnnanimate.Chipset(this.playerHead, this.winplayer, data, this.quanti, this.Grp_cz, this.chip, null);
        }, 9)
        this.s_timer6 = this.scheduleOnce(()=> {
            // this.recordWinner(mdata);
            // brnnanimate.Chipset(this.playerHead, this.winplayer, mdata, this.quanti, this.Grp_cz, this.chip, null);
            brnnanimate.Floatword(data, this.winlose_gold_pos, this.win_gold, this.lose_gold, 0);
            brnnanimate.WinPlayerlight(data, this.playerHead);
            this.Updategold(data);
        }, 11)
        
        if( cc.isValid( this.jiazaiAnim ))
        {
            this.jiazaiAnim.active = false;
        }
    }
