    /**
    * 
    * @param spin 
    * @param index  index
    * @param anim_name  
    * @param fun  
    */
    playAnim(spin: any, index: number, anim_name: string, isloop: boolean, fun: Function) {
        if ( !cc.isValid( spin ) ) {
            cc.log("!")
            return;
        }
        spin.active = true;
        let ske_0 = spin.getComponent(sp.Skeleton);
        spin = ske_0;
        spin.clearTracks();
        spin.addAnimation(index, anim_name, isloop);

        var timer = setTimeout(() => {
            clearTimeout(timer)
            fun && fun()
        }, .5 * 1000)
    }

