    //n()
    roundFun(value, n) {
        return Number(Math.round(value*Math.pow(10,n))/Math.pow(10,n));
    }

    //n()
    floorFun(value, n) {
        return Number((Math.floor(Math.round(value*Math.pow(10,n)))/Math.pow(10,n)).toFixed(n));
    }
    
    /**
     *  
     * @param number 
     */
    removeDecimalpoint(number) {
        number = number.toFixed(6)
        let str = number.toString()
        if (str.includes(".")) {
            return str.substring(0, str.indexOf(".") + 3)
        } else {
            return str
        }
    }

    /**
     *  0 
     * @param number 
     */
    displaytwodecimalplaces(number) {
        let number1 = number.toString().split(".");
        if (number1.length == 1) {
            return parseFloat(number);
        }
        if (number1.length > 1) {
            if (number1[1].length < 2) {
                number = number.toString() + "0";
            }
            if (number1[1].length > 2) {
                number = number1[0] + "." + number1[1].substring(2, 0);
            }
            return number;
        }
    }
    /**
   * . / 
   * @param gold 
   */
    Conversion(gold: number) {
        var str: string = "";
        str = gold.toString().replace(".", "/")
        return str;
    }
    /**
     * 
     * @param node node
     * @param url   ""
     */
    loadSpriteFrame(node: any, url: string) {
        if ( !cc.isValid( node ) ) {
            return;
        }
        let spr = node.getComponent(cc.Sprite);
        cc.loader.loadRes(url, cc.SpriteFrame, function (err, spriteFrame) {
            if (err) {
                cc.log("url:" + url + " , Node:"+ node+ " , spr:"+ spr+"  err="+err);
                return;
            }
            if( cc.isValid( spr ) )
            {
                spr.spriteFrame = spriteFrame;
            }
        });
    }
    /**
       * 
       * @param node node
       * @param atlasurl   ""
       * @param url   ""
       */
    loadSpriteAtlasFrame(node: any, atlasurl: string, url: string) {
        if (node == null) {
            return;
        }
        cc.loader.loadRes(atlasurl, cc.SpriteAtlas, function (err, atlas) {
            if (err) {
                cc.log("");
                return;
            }
            if( cc.isValid( node ) )
            {
                let sp = node.getComponent(cc.Sprite);
                let spm:cc.SpriteFrame = null;
                if( cc.isValid( atlas ) )
                {
                    spm = atlas.getSpriteFrame( url );
                }
                if( cc.isValid( sp ) )
                {
                    if( cc.isValid( spm ) )
                    {
                        sp.spriteFrame = spm;
                    }
                }
            }
        })
    }
    /**
     * 
     * @param node node
     * @param url   ""
     * @param imgtype   (png jpg)
     */
    loadNetworkSpriteFrame(node: any, url: string, imgtype: string) {
        if( cc.isValid( node ) )
        {
            let spr = node.getComponent(cc.Sprite);
            cc.loader.load({ url: url, type: imgtype }, (err, img) => {
                if( err )
                {
                    cc.log( " url=",url);
                    return;
                }
                if( cc.isValid( spr ) )
                {
                    if( cc.isValid( img ) )
                    {
                        spr.spriteFrame = new cc.SpriteFrame(img);
                    }
                }
            });
        }
    }
    /**
     * 
     * @param num 
     */
    img(num: number) {
        var str: string = "";
        switch (num) {
            case 0: str = "chips/chip_green-s"; break;
            case 1: str = "chips/chip_blue-s"; break;
            case 2: str = "chips/chip_purple-s"; break;
            case 3: str = "chips/chip_red-s"; break;
            case 4: str = "chips/chip_orange-s"; break;
            default:
                break;
        }
        return str;
    }

    /**
     *  
     * @param lower 
     * @param upper 
     */
    random(lower, upper) {
        return Math.floor(Math.random() * (upper - lower + 1)) + lower;
    }

    /**
         * 
         * @param arr 
         * @param num 
         */
    chunk(arr, num) {
        num = num * 1 || 1;
        var ret = [];
        arr.forEach(function (item, i) {
            if (i % num === 0) {
                ret.push([]);
            }
            ret[ret.length - 1].push(item);
        });
        // cc.log(ret);
        return ret;
    };


