const { ccclass, property } = cc._decorator;

@ccclass
export default class brnnButtonSclear extends cc.Component {

    @property({ tooltip: '' })
    pressedScale: number = 1;
    @property({ tooltip: '' })
    transDuration: number = 0;


    private initScale: number = 1;

    private scaleDownAction: any = null;
    private scaleUpAction: any = null;
    onLoad() {
        this.initScale = this.node.scale;
        // this.button = this.getComponent(cc.Button);
        this.scaleDownAction = cc.scaleTo(this.transDuration, this.pressedScale);
        this.scaleUpAction = cc.scaleTo(this.transDuration, this.initScale);
        let onTouchDown = (event)=> {
            this.node.stopAllActions();
            this.node.runAction(this.scaleDownAction);
        }
        let onTouchUp = (event)=> {
            this.node.stopAllActions();
            this.node.runAction(this.scaleUpAction);
        }
        this.node.on('touchstart', onTouchDown, this.node);
        this.node.on('touchend', onTouchUp, this.node);
        this.node.on('touchcancel', onTouchUp, this.node);
    }

    start() {

    }

    // update (dt) {}
}
