import { events, EventKind } from '../brnntools/brnn_event';
import { msg, msg as Msg } from "../brnnproto/brnn_proto_msg";
import { configuration } from '../brnnutils/brnnConfiguration';

type IResponseFunc = (msg: any) => void;

const { ccclass } = cc._decorator;



@ccclass
class Client{
    private static _instance: Client;
    private io: WebSocket;
    private timer: number;
    private mapHandler:Map< Msg.SubCommand, any>;
    private ReConnectCount:number;
    private curRootNode:cc.Node;

    constructor() {
        this.timer = 0;
        this.io = undefined;
        this.mapHandler = new Map();
        this.ReConnectCount = 0;
        this.curRootNode = null;

        this.mapHandler[ Msg.SubCommand.EnumSubHeartBeatReq ] = Msg.HeartBeatReq;
        this.mapHandler[ Msg.SubCommand.EnumSubHeartBeatResp ] = Msg.HeartBeatResp;
        this.mapHandler[ Msg.SubCommand.EnumSubLoginReq ] = Msg.LoginReq;
        this.mapHandler[ Msg.SubCommand.EnumSubLoginResp ] = Msg.LoginResp;
        this.mapHandler[ Msg.SubCommand.EnumSubUserBetReq ] = Msg.UserBetReq;
        this.mapHandler[ Msg.SubCommand.EnumSubUserBetResp ] = Msg.UserBetResp;
        this.mapHandler[ Msg.SubCommand.EnumSubGameStart ] = Msg.GameStart;
        this.mapHandler[ Msg.SubCommand.EnumSubGameStartBet ] = Msg.GameStartBet;
        this.mapHandler[ Msg.SubCommand.EnumSubGameAward ] = Msg.GameAward;

        events.register(EventKind.C2S_PlayerBet, "send", this.BetRequest.bind(this));
    }

    public static getInstance(): Client {
        this._instance || (this._instance = new Client());
        return this._instance;
    }

    setCurSceneRootNode( node:cc.Node )
    {
        this.curRootNode = node;
    }

    /**
     * name
     */
    public init(): void {
        this.io || this.initSocket();
    }

    // public registerCallBack(kind: msg.msg.MessageID, func: IResponseFunc): void {
    //     this.mapHandler.set(kind, func);
    // }

    public registerCallBack(kind: number, func: IResponseFunc): void {
        // this.mapHandler.set(kind, func);
    }
    /**
     *  websocket
     * @param 
     */
    public closeWebsock() {
        cc.log('websocket');
        if( this.io )
        {
            this.io.close();
            this.stopPing( null , false );
            this.io.onopen = this.io.onerror = this.io.onclose = this.io.onmessage = null;
            this.io = null;
            if (this.stop != null) {
                clearTimeout(this.stop);
                this.stop = null;
            }
            clearInterval(this.timer);
            this.timer = null;
        }
    }
    public initSocket() {
        this.closeWebsock();

        let ServerURL = "ws://127.0.0.1:1228";
        if ((cc.sys.platform === cc.sys.ANDROID || cc.sys.os === cc.sys.OS_ANDROID) && ServerURL.indexOf('wss://') == 0) {
            this.io = new WebSocket(ServerURL, {}, cc.url.raw('resources/hall/cacert.pem'));
        } else {
            this.io = new WebSocket(ServerURL);
        }

        // this.io = new WebSocket(app.ServerURL);
        this.io.binaryType = "arraybuffer";
        cc.log(":", ServerURL);
        this.io.onopen = (evt: Event) => {
            cc.log("");
            this.startPing();
            this.LoginTest();
            // events.dispatch(EventKind.S2C_Connect);
        }
        this.io.onmessage = (evt: MessageEvent) => {
            this.onReceiveMessageEvent(evt);
        }
        this.io.onerror = (evt: Event) => {
            cc.log("");
            this.stopPing( evt );
        }
        this.io.onclose = (evt: CloseEvent) => {
            cc.log("");
            this.stopPing( evt , true);
        }
    }
    private onReceiveMessageEvent(evt: MessageEvent): void {
        this.parseProtoBufId(evt);
    }
    /**
     * Uint8Array[]int
     * 4|=int
     * @param uint8Ary Uint8Array
     * @return int
     */
    private Uint8ArrayToInt(uint8Ary: Uint8Array): number {
        let retInt = 0;
        for (let i = 0; i < uint8Ary.length; i++) {
            retInt |= (uint8Ary[i] << (8 * (uint8Ary.length - i - 1)));
        }
        return retInt;
    }
    private parseProtoBufId(buffer: MessageEvent) {
        const arrayBuffer: ArrayBuffer = buffer.data;
        let dataUnit8Array = new Uint8Array(arrayBuffer);
        //
        const id = this.Uint8ArrayToInt(dataUnit8Array.slice(0, 2));
        dataUnit8Array = dataUnit8Array.slice(2);
        this.distrubuteMsg(id, dataUnit8Array)
    }

    private distrubuteMsg(id, data) {
        let bodyClass: any = this.mapHandler[ id ];
        if(!bodyClass){
            cc.warn(' distrubuteMsg id', id);
            return;
        }
        const gameMsg: any = bodyClass.decode(data);
        cc.log("id:" + id , "data:", gameMsg );
        this.Recive(id, gameMsg);

    }

    /**
     * 
     * @param id        id 
     * @param data      
     * @param isQueue    
     * @param trycount  
     */
    public Recive(p_id : number, p_data : any , isQueue:boolean = false , trycount:number = 0 ){ //json
        if(  p_data.resultCode == null || p_data.resultCode ==Msg.ResultCode.Success ) 
        {
            switch( p_id )
            {
                case Msg.SubCommand.EnumSubHeartBeatResp:
                    break;
                case Msg.SubCommand.EnumSubLoginResp:
                    configuration.initUserInfo( p_data );
                    break;
                case Msg.SubCommand.EnumSubUserBetResp:
                    cc.log("Msg.SubCommand.EnumSubUserBetResp============")
                    cc.log("p_data.result=",p_data.result)
                    if( p_data.result )
                    {
                        cc.log( "p_data.result.resultCode=",p_data.result.resultCode )
                    }
                    events.dispatch(EventKind.S2C_PlayerBet, p_data);
                    break;
                case Msg.SubCommand.EnumSubGameStart:
                    events.dispatch(EventKind.S2C_GameStart, p_data);
                    break;
                case Msg.SubCommand.EnumSubGameStartBet:
                    events.dispatch(EventKind.S2C_GameStartBet, p_data);
                    break;
                case Msg.SubCommand.EnumSubGameAward:
                    events.dispatch(EventKind.S2C_GameAward, p_data);
                    break;
                default:
                    break;
            }
        }
        else
        {
            events.dispatch(EventKind.S2C_Err, p_data);
        }
    }
    /** */
    private LoginTest() {
        this.sendMessage( Msg.SubCommand.EnumSubLoginReq , Msg.LoginReq.create());
        cc.log('');
    }

    // private startPing(): void {
    //     this.timer = setInterval(() => {
    //         // cc.log("    " + msg.MessageID.Ping);
    //         this.sendMessage(msg.MessageID.MsgPing, {});
    //     }, 5000);
    // }

    private startPing(): void {
        this.timer = setInterval(() => {
            // cc.log("    " + 0);
            this.sendMessage( Msg.SubCommand.EnumSubHeartBeatReq, Msg.HeartBeatReq.create() );
        }, 5000);
    }
    stop: any = null;
    private stopPing(evt:any , reconnect: boolean = true): void {
        if (this.timer) {
            clearInterval(this.timer);
            this.timer = 0;
        }
        if (reconnect) {
            if (this.stop == null) {
                let tryConnect = ()=>{
                    if (this.io.readyState == WebSocket.OPEN) {
                        return;
                    }
                    this.ReConnectCount++;
                    events.dispatch(EventKind.S2C_ServerconnectClose, evt);
                    this.initSocket();
                    this.stop = null;
                }
                if( this.ReConnectCount == 0 )
                {
                    tryConnect();
                }
                this.stop = setTimeout(tryConnect, 2800)
            }
        }
    }

    /**
     * 
     * @param kind 
     * @param data 
     */
    public sendMessage(kind: number, data: any): void {
        if (this.io == null) return;
        if (this.io.readyState !== WebSocket.OPEN) {
            return;
        }
        let bodyClass: any = this.mapHandler[ kind ];
        if(!bodyClass){
            cc.warn('sendMessage id', kind);
            return;
        }
        const message = bodyClass.create(data);
        cc.log(":", message);
        const buffer = bodyClass.encode(message).finish();
        // leaf 
        const addTagBuffer = this.protoBufAddTag(kind, buffer);
        this.io.send(addTagBuffer.buffer);
    }
    private protoBufAddTag(tag: number, buffer: Uint8Array): Uint8Array {
        const bufferAddTag = new Uint8Array(buffer.length + 2);
        const tagBinary = this.IntToUint8Array(tag, 16);
        const tagUnit8 = new Uint8Array(tagBinary);

        bufferAddTag.set(tagUnit8, 0);
        bufferAddTag.set(buffer.subarray(0, buffer.length), 2);

        return bufferAddTag;
    }
    private IntToUint8Array(num: number, bits: number): number[] {
        const resArry = [];
        const xresArry = [];
        const binaryStr: string = num.toString(2);
        for (const chr of binaryStr) {
            resArry.push(parseInt(chr, 10));
        }

        if (bits) {
            for (let r = resArry.length; r < bits; r++) {
                resArry.unshift(0);
            }
        }

        const resArryStr = resArry.join("");
        for (let j = 0; j < bits; j += 8) {
            xresArry.push(parseInt(resArryStr.slice(j, j + 8), 2));
        }

        return xresArry;
    }

    private BetRequest( data:Msg.IUserBetReq )
    {
        this.sendMessage( Msg.SubCommand.EnumSubUserBetReq , Msg.UserBetReq.create( data ));
    }
}

export const websocket = Client.getInstance();
