
const { ccclass, property } = cc._decorator;

@ccclass
export default class promptbox extends cc.Component {

    onLoad() {

    }
    initPrompt() {
        let action = cc.moveTo(1.5, cc.v2(0, 150));
        let action2 = cc.moveTo(0.5, cc.v2(0, 150));
        this.node.runAction(cc.sequence(action, action2, cc.callFunc(() => {
            if( cc.isValid( this.node ) )
            {
                this.node.destroy();
            }
        })))
    }

    // update (dt) {}
}
