const { ccclass, property } = cc._decorator;

@ccclass
class AudioTool {

    private Volume: number = 1;
    public Mute: boolean = false; ss
    /** */
    private _audios: cc.AudioSource[] = [];

    protected static instance: AudioTool;
    public static getInstance(): AudioTool {
        if (!this.instance) {
            this.instance = new AudioTool();
        }
        return this.instance;
    }
    /** */
    loadSoundsRes( tempnode:cc.Node ) {
        let thda = this;
        thda._audios = [];
        cc.loader.loadResDir('brnn/brnn_audio', cc.AudioClip, function (err, clips) {
            if (err) {
                cc.log("");
                return;
            }
            for (let i = 0; i < clips.length; i++) {
                if( cc.isValid( clips[ i ] ))
                {
                    let presence = thda.judgmentsfcz(clips[i].name);
                    if (presence) {
                        return;
                    }
                    else {
                        let audioSource = tempnode.addComponent( cc.AudioSource );
                        audioSource.clip = clips[i];
                        audioSource.name = clips[i].name;
                        thda._audios.push(audioSource);
                    }
                }
            }
            cc.log("");
        });
    }
    /** */
    LoadAudio(index: number) {
        var str: string = "";
        switch (index) {
            case 0: str = "00_meiniu"; break;        //
            case 1: str = "01_niuyi"; break;         //1
            case 2: str = "02_niuer"; break;         //2
            case 3: str = "03_niusan"; break;        //3
            case 4: str = "04_niusi"; break;         //4
            case 5: str = "05_niuwu"; break;         //5
            case 6: str = "06_niuliu"; break;         //6
            case 7: str = "07_niuqi"; break;         //7
            case 8: str = "08_niuba"; break;         //8
            case 9: str = "09_niujiu"; break;         //9
            case 10: str = "10_niuniu"; break;        //
            case 11: str = "11_sizha"; break;         //
            case 12: str = "12_wuhuaniu"; break;      //
            case 13: str = "brnn_bgm"; break;         //
            case 14: str = "brnn_fapai"; break;        //
            case 15: str = "brnn_lose"; break;        //
            case 16: str = "brnn_win"; break;        //
            case 17: str = "brnn_zjtongchi2"; break;    //
            case 18: str = "brnn_zjtongpei"; break;        //
            case 19: str = "p_bet_long"; break;        //0
            case 20: str = "p_bet_mid"; break;        //1
            case 21: str = "p_bet_short"; break;        //2
            case 22: str = "p_bet_short2"; break;        //3
            case 23: str = "p_change_banker2"; break;    //
            case 24: str = "p_click"; break;             //
            case 25: str = "p_clickcm"; break;        //
            case 26: str = "p_clickxiazhuqu"; break;        //
            case 27: str = "p_collectgold2"; break;     //
            case 28: str = "p_countdown"; break;     // ()
            case 29: str = "p_dating_rm_bgm"; break;     //
            case 30: str = "p_dplayer_bet"; break;        //
            case 31: str = "p_remind"; break;        //  ()
            case 32: str = "p_start_bet"; break;     //
            case 33: str = "p_stop_bet"; break;     //
            case 34: str = "p_tip_remind"; break;     /// 
            case 35: str = "zhuangjiatuntu"; break;     // 
            case 36: str = "opencard"; break;     // 
            case 37: str = "p_player_come_In"; break;     // 
            case 38: str = "p_button_click"; break;     // 

            default:
                break;
        }
        return str;
    }
    /** */
    judgmentsfcz(name: string) {
        let thda = this;
        for (let i = 0; i < thda._audios.length; i++) {
            if (thda._audios[i].name == name) {
                return true;
            }
        }
        return false;
    }
    /**0.0 ~ 1.0*/
    volume_udioSource(num: number) {
        let thda = this;
        this.Volume = num;
        for (let i = 0; i < thda._audios.length; i++) {
            thda._audios[i].volume = this.Volume;
        }
    }
    /**true-   false-*/
    volume_iudioSource(bol: boolean) {
        let thda = this;
        this.Mute = bol;
        for (let i = 0; i < thda._audios.length; i++) {
            if( cc.isValid( thda._audios[i] ))
            {
                if( cc.isValid( thda._audios[ i ].audio ) )
                {
                    thda._audios[i].mute = this.Mute;
                }
                else
                {
                    cc.log( "thda._audios[ i ].audio is null thda._audios[i]="+thda._audios[i].name);
                }
            }
        }
    }
    /**
     * strname 
     */
    play_audioSource(strname: string , volume:number = 1 ) {
        let thda = this;
        let findid = -1;
        for (let i = 0; i < thda._audios.length; i++) {
            if( cc.isValid( thda._audios[i] ) )
            {
                if (strname == thda._audios[i].name) {
                    findid = i;
                    if( !( thda._audios[ i ].audio.getState() == 1 ) )
                    {
                        thda._audios[i].volume = volume;
                        thda._audios[i].play();
                        return;
                    }
                }
            }
        }
        if( findid >= 0 && findid < thda._audios.length )
        {
            let audioSource = thda._audios[ findid ].node.addComponent( cc.AudioSource );
            audioSource.clip = thda._audios[ findid ].clip;
            audioSource.name = thda._audios[ findid ].name;
            audioSource.volume = volume;
            audioSource.mute = thda._audios[ findid ].mute;
            thda._audios.push(audioSource);
            audioSource.play();
        }
    }
    /** */
    stop_audioSource() {
        let thda = this;
        for (let i = 0; i < thda._audios.length; i++) {
            thda._audios[i].stop();
        }
    }
    /** */
    pause_audioSource() {
        let thda = this;
        for (let i = 0; i < thda._audios.length; i++) {
            thda._audios[i].pause();
        }
    }
    /** */
    resume_audioSource() {
        let thda = this;
        for (let i = 0; i < thda._audios.length; i++) {
            thda._audios[i].resume();
        }
    }
    /** */
    rewind_audioSource(index: number) {
        let thda = this;
        thda._audios[index].rewind();
    }
    /** */
    Removeaudios() {
        for (let i = 0; i < this._audios.length; i++) {
            if (this._audios[i] != null) {
                this._audios[i] = null;
            }
        }
        this._audios = [];
    }
}

export const audiotool = AudioTool.getInstance();